<template>
  <div class="top">
    <div class="logo">
      <router-link :to='{name: "admin"}'>
        <img src="@/assets/logo.png" alt=""> 新盛和阳果蔬生鲜市场管理系统
      </router-link>
    </div>
    <div class="menu" v-if="isLogin">
      欢迎您, admin <span class="logout" @click="logout">退出</span>
    </div>
  </div>
</template>

<script>
  export default {
    computed: {
      isLogin() {
        let token = localStorage.getItem("token")
        return token ? true : false
      }
    },
    methods: {
      logout() {
        localStorage.removeItem("token");
        this.$router.push({
          name: "登录"
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .top {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 64px;
    background-color: #fff;
    box-shadow: 0 0 10px #999;
    display: flex;
    .logo {
      margin-top: 12px;
      padding-left: 20px;
      font-size: 18px;
      flex: 1;
      img {
        height: 40px;
        vertical-align: middle;
      }
      a {
        color: #000;
      }
    }
    .menu {
      display: inline-block;
      padding-left: 20px;
      padding-right: 20px;
      line-height: 24px;
      padding-top: 20px;
    }
    .logout {
      color: blue;
      cursor: pointer;
      &:hover {
        text-decoration: underline;
      }
    }
  }
</style>